import React from "react";
import {Button, Input, Checkbox} from "antd";
import 'antd/dist/antd.css';
import './Css/login.css'

function LoginButton() {
    return (
        <Button id='loginButton' className='button' type='primary' >登录</Button>
    )
}

export default class Login extends React.Component {
    
    constructor(props) {
        super(props)
        this.state = {
            "username": '',
            "password": '',
        }
    }

    render() {
        return (
            <div className='parent'>
                <div className='container'>
                    <Input id='username' className='username' type="text" placeholder='请输入用户名'/>
                    <Input id='password' className='password' type="password" placeholder='请输入用密码'/>
                    <LoginButton />
                </div>
            </div>
        )
    }
} 